.plus {
  display: inline-block;
  width: 8px;
  height: 8px;
  position: relative;

  &:after,
  &:before {
    content: '';
    position: absolute;
    background-color: $brown; }

  &:after {
    bottom: 3px;
    right: 0;
    width: 8px;
    height: 2px; }

  &:before {
    bottom: 0;
    right: 3px;
    width: 2px;
    height: 8px;
    @include tr(all .3s ease); } }
