  line-height: 90px;
  position: relative;
  text-align: center;
  background-color: #fff;
  z-index: 5;
}

.nav__el {
  display: inline-block;
}

.nav__el:hover .nav__dropdown {
  display: block;
}

.nav__link {
  display: inline-block;
  padding: 0 22px;
  font-family: raleway, sans-serif;
  font-weight: 200;
  transition: all .3s ease-in-out;
}

.nav__link:hover {
  color: #f06060;
}

.nav__link:hover .plus:after,
.nav__link:hover .plus:before {
  background-color: #f06060;
}

.nav__link:hover .plus:before {
  height: 0;
}

.nav__dropdown {
  position: absolute;
  display: none;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  padding: 50px;
  width: 1000px;
  background-color: #fff;
  text-align: center;
  box-sizing: border-box;
  box-shadow: 0 10px 30px 0 rgba(92, 75, 81, 0.1);
  z-index: 10;
}

.plus {
  display: inline-block;
  width: 8px;
  height: 8px;
  position: relative;
}

.plus:after, .plus:before {
  content: '';
  position: absolute;
  background-color: #5c4b51;
}

.plus:after {
  bottom: 3px;
  right: 0;
  width: 8px;
  height: 2px;
}

.plus:before {
  bottom: 0;
  right: 3px;
  width: 2px;
  height: 8px;
  transition: all 0.3s ease;
}

