.btn.is-big {
  height: 61px;
  line-height: 61px;
  max-width: 280px;
  padding: 0 87px;
  font-weight: 600;
  background-color: #f06060;
  border-right: 3px;
  letter-spacing: 1px;
  color: #fff;
  position: relative;
  overflow: hidden;
  z-index: 10;
}

.btn.is-big:hover {
  color: #fff;
}

.btn.is-big:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120%;
  height: 550%;
  display: block;
  transform: translate3d(-50%, -50%, 0) scale(0);
  transform-origin: 50% 50%;
  border-radius: 50%;
  background-color: #5c4b51;
  opacity: 0;
  z-index: -10;
  transition: all .3s ease-in-out;
}

.btn.is-big:hover:before {
  transform: translate3d(-50%, -50%, 0) scale(1);
  opacity: 1;
}

.btn.is-small {
  height: 35px;
  line-height: 35px;
  padding: 0 10px;
  font-size: 20px;
}

.btn.is-top-right {
  position: absolute;
  top: 0;
  right: 0;
}

.btn.is-red {
  color: #f06060;
  background-color: transparent;
}

.btn.is-red:hover {
  color: #5c4b51;
}

