.container {
  width: 1070px;
  margin: 0 auto;
  @include clr; }

.l-col-25 {
  width: 25%; }

.l-col-20 {
  width: 20%; }

.l-col-60 {
  width: 60%; }

.header {
  width: 100%;
  text-align: center;
  @include clr;

  &__top {
    padding: 10px 0;
    @include clr; }

  &__left {
    float: left; }

  &__center {
    float: left; }

  &__right {
    float: left; } }

.footer {
  @include clr;

  &__top {
    padding: 35px 0; }

  &__col {
    float: left; } }
