body {
	font-family: raleway, sans-serif;
	font-weight: 200;
	font-size: 16px;
	color: #5c4b51;
	background-color: #f5f4f4;
	line-height: 1.6; }

//variables
$brown: #5c4b51;
$red: #f06060;
$blue: #8cbeb2;
$yellow: #f3b562;
$grey: #ada2a4;
$bgGrey: #f5f4f4;

@include font('bitter400', bitter-regular);
@include font('bitter400ital', bitter-italic);
@include font('bitter600', bitter-bold);

@mixin h400 {
	font-family: 'bitter400', Arial, sans-serif; }

@mixin h400i {
	font-family: 'bitter400ital', Arial, sans-serif; }

@mixin h600 {
	font-family: 'bitter600', Arial, sans-serif; }


//typography
h1 {
	font-size: 32px; }

h2 {
	font-size: 28px; }

h3 {
	font-size: 26px; }

h4 {
	font-size: 24px; }

h5 {
	font-size: 16px; }

h6 {
	font-size: 13px; }

a {
	display: block;
	color: $brown;
	text-decoration: none;
	cursor: pointer;
	@include tr(all .3s ease-in-out); }

p {
	line-height: 1.6; }

img {
	display: block; }

// common blocks
.i-weight-100 {
	font-weight: 100; }

.i-weight-200 {
	font-weight: 200; }

.i-weight-300 {
	font-weight: 300; }

.i-weight-400 {
	font-weight: 400; }

.i-weight-500 {
	font-weight: 500; }

.i-weight-600 {
	font-weight: 600; }

.i-weight-700 {
	font-weight: 700; }

.is-blue {
	background-color: $blue; }

.is-red {
	background-color: $red; }

.is-yellow {
	background-color: $yellow; }

.is-brown {
	color: $brown; }

.is-white {
	color: #fff; }

.i-bold {
	font-family: 'Bitter', serif;
	font-weight: 600; }

.i-ital {
	font-style: italic; }

.is-active {
	color: $red; }

.i-text-upper {
	text-transform: uppercase; }

.i-text-center {
	text-align: center; }

.i-text-left {
	text-align: left; }

.i-mb-55 {
	margin-bottom: 55px; }

.i-pt-20 {
	padding-top: 20px; }

.bg-white {
	background-color: #fff; }

.offer {
	position: relative;
	font-size: 19px;
	font-family: 'Bitter', serif;
	font-weight: 600;

	&:after {
		content: '';
		position: absolute;
		bottom: -10px;
		right: 0;
		height: 3px;
		width: 130px;
		background-color: $yellow; } }

.insta {
	padding: 50px 0;
	border-bottom: 2px solid #fff;

	&__top {
		text-align: center; }

	&__descr {
		color: $grey; } }

.cart,
.currency {
	font-family: raleway, sans-serif;
	font-weight: 400;
	color: $brown;
	cursor: pointer;
	@include tr(all .3s ease-in-out);
	&:hover {
		color: $red; } }

.cart {
	i {
		font-size: 25px; } }

.swiper-container {
	max-width: 640px;
	min-height: 310px;

	&.is-product {
		max-width: none; } }

.swiper-button-next,
.swiper-button-prev {
	background-image: none;
	position: absolute;
	top: 275px; }

.arrow-button-next {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 40px;
	height: 2px;
	background-color: $brown;
	z-index: 100;
	cursor: pointer; }

.arrow-button-prev {
	position: absolute;
	bottom: 0;
	right: 0;
	width: 40px;
	height: 2px;
	background-color: $brown;
	z-index: 100;
	cursor: pointer; }

.icon {
	display: inline-block;

	&.is-arrow-right {
		width: 15px;
		height: 15px;

		&:before {
			border-color: transparent transparent #5c4b51 transparent; } }

	//&:before
	//argin-top: -5px
	//isplay: block
	//ontent: ''
	//osition: absolute
	//idth: 40px
 }	//eight: 2px

.section-title {
	position: relative;
	text-align: center;
	padding-bottom: 22px; }

.season-item {
	position: relative;
	text-align: center;
	width: 100%;
	height: 440px;
	background-size: cover;
	background-repeat: no-repeat;

	&__descr {
		font-size: 26px; }

	&.bg-tomato {
		background-image: url("../img/slide_1.jpg");
		background-position: 50% 68%; }

	&.bg-pepper {
		background-image: url("../img/slide_2.jpg");
		background-position: 50% 68%; }

	&.bg-apple {
		background-image: url("../img/slide_3.jpg");
		background-position: 50% 68%; }

	.badge {
		position: absolute;
		top: 26%;
		left: 33%; } }

.sale {
	width: 100%;
	position: relative;
	cursor: pointer;
	box-sizing: border-box;
	@include clr;

	&__left,
	&__right {
		width: 50%;
		float: left;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center center;
		height: 540px;
		text-align: center;
		border-bottom: 3px solid #fff;
		border-top: 3px solid #fff;

		&.bg-fruits {
			background-image: url("../img/fruits.jpg"); }

		&.bg-rasper {
			background-image: url("../img/raspber.jpg"); } }

	&__left {
		border-right: 3px solid #fff;

		.badge {
			position: absolute;
			top: 50%;
			left: 9%; }

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: $red;
			opacity: 0;
			transition: opacity .3s ease-in-out; }

		&:hover:before {
			opacity: .2; } }

	&__right {
		.badge {
			position: absolute;
			top: 50%;
			right: 27%; }

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: $red;
			opacity: 0;
			transition: opacity .3s ease-in-out; }

		&:hover:before {
			opacity: .2; } }

	&__title {
		padding-top: 30px; }

	&__descr {
		font-family: 'Bitter', serif;
		font-weight: 600;
		font-size: 23px; } }









